.full_height{
	height: 97.5vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.home-intro {
	height: 100vh !important;
    overflow: hidden;
}

.home-intro-container {
    width: 100%;
    overflow: hidden;
    background-position: center;
    height: auto;
}

.home-intro-container .overlay {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    z-index: 2;
    background: black;
    opacity: 0.6;
}

.home-intro-image {
    width: 100%;
}

.home-intro-content {
    position: absolute;
    top: 15vh;
    left: 10vw;
    z-index: 100;
}

.home-intro-content > h1 > strong {
    font-size: 60px;
    color: white;
}

.home-intro-content > h2 {
    color: white;
}

.center-align-text {
    text-align: center !important;
}

.custom_yellow {
	color : #FFFF00	;
}

.custom_blue {
	color : #3abcc1;
}

.sidebar {
	background-color : #eaeaea;
}

.social-icons {
  list-style: none;
  padding: 0;
  margin: 0;
}
.social-icons li {
  display: inline-block;
  font-size: 16px;
  margin-right: 10px;
}
.social-icons li a {
  color: #fff;
  display: inline-block;
  padding: 6px;
  background-color: #3d3d3d;
  border-radius: 100px;
}
.social-icons li a:before {
  width: 16px;
  height: 16px;
  display: inline-block;
  text-align: center;
}


.social-icons .icons,
.social-icons :before,
.social-icons .icon .fa {
  transition: all 0.35s;
  transition-timing-function: cubic-bezier(0.31, -0.105, 0.43, 1.59);
}
.social-icons .icon:before {
  top: 90%;
  left: -110%;
}
.social-icons .icon .fa {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
.social-icons .icon.facebook:before {
  background-color: #3b5998;
}
.social-icons .icon.facebook .fa {
  color: #3b5998;
}

.social-icons .icon.linkedin:before {
  background-color: #3b5998;
}
.social-icons .icon.linkedin .fa {
  color: #3b5998;
}

.social-icons .icon.twitter:before {
  background-color: #3cf;
}
.social-icons .icon.twitter .fa {
  color: #3cf;
}
.social-icons .icon.youtube:before {
  background-color: #dc4a38;
}
.social-icons .icon.youtube .fa {
  color: #dc4a38;
}
.social-icons .icon.dribbble:before {
  background-color: #f26798;
}
.social-icons .icon.dribbble .fa {
  color: #f26798;
}
.social-icons .icon.skype:before {
  background-color: #00aff0;
}
.social-icons .icon.skype .fa {
  color: #00aff0;
}
.social-icons .icon:focus:before,
.social-icons .icon:hover:before {
  top: -10%;
  left: -10%;
}
.social-icons .icon:focus .fa,
.social-icons .icon:hover .fa {
  color: #fff;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.social-icons {
  height: 90px;
  margin: auto;
  font-size: 0;
  text-align: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.social-icons .icon {
  display: inline-block;
  background-color: #fff;
  width: 60px;
  height: 60px;
  line-height: 60px;
  margin: 0 6px;
  text-align: center;
  position: relative;
  overflow: hidden;
  border-radius: 28%;
  box-shadow: 0 3px 10px -3px rgba(0,0,0,0.1);
  opacity: 0.99;
}
.social-icons .icon:before {
  content: '';
  width: 120%;
  height: 120%;
  position: absolute;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.social-icons .icon .fa {
  font-size: 25px;
  vertical-align: middle;
}
